<template>
  <div class="account-wrapper">
    <div class="head">
      <div class="head-bg"></div>
      <div class="center">
        <div class="logout" @click="logout">退出登录</div>
        <div class="user-info">
          <div class="image">
            <van-image :src="user.associated?user.associated.officia_account_of_wechat.avatar_url:'url'">
              <template v-slot:error>
                <van-image src="../../../../static/image/mobile/tx.png" />
              </template>
            </van-image>
          </div>
          <ul>
            <li>
              <label>用户名：</label>
              <span>{{user.user_name}}</span>
              <i class="el-icon-edit-outline" @click.stop="show=true"></i>
            </li>
            <li>
              <label>用户编号：</label>
              <span>{{user.uid}}</span>
            </li>
          </ul>
          <div class="right-bg"></div>
        </div>
      </div>
    </div>
    <van-cell-group>
      <van-cell center title="绑定手机" :label="user.phone_num" value="去修改" is-link />
      <!-- <van-cell center title="绑定邮箱" label="绑定后，可提高账号防御能力" value="去修改" is-link /> -->
      <van-cell title="密码" v-if="user.note.pass_set_status" value="已设置" is-link />
      <van-cell title="密码" v-else value="设置" is-link />
    </van-cell-group>
    <div class="personal-title">第三方应用</div>
    <van-cell-group>
      <van-cell center>
        <template #title>
          <div class="icon-font wechat">
            <van-icon class-prefix="iconfont icon-48-38-60-47" name="icon-48-38-60-47" />
          </div>
          <span class="custom-title">微信</span>
          <span
            class="custom-name"
          >{{user.associated?user.associated.officia_account_of_wechat.nickname:"未绑定"}}</span>
        </template>
        <template #default>
          <span v-show="user.associated" @click="removebind">解绑</span>
          <span v-show="!user.associated">绑定</span>
        </template>
      </van-cell>
      <!-- <van-cell center>
        <template #title>
          <div class="icon-font zhifubao">
            <van-icon class-prefix="iconfont icon-48-38" name="icon-48-38" />
          </div>
          <span class="custom-title">支付宝</span>
          <span class="custom-name">小芳</span>
        </template>
        <template #default>
          <span class="not-color">未绑定</span>
        </template>
      </van-cell>-->
    </van-cell-group>
    <van-dialog
      v-model="show"
      title="用户名"
      @confirm="save"
      :beforeClose="actionClose"
      show-cancel-button
    >
      <form action ref="form-wrapper" class="form-wrapper">
        <div class="input-wrapper">
          <div class="text-input">
            <input type="text" v-model="form.user_name" placeholder="请输入新用户名" />
          </div>
        </div>
        <div class="submit-wrapper" v-if="false">
          <button type="submit" @click.prevent="function(){return false}" class="submit">登录</button>
        </div>
      </form>
    </van-dialog>
  </div>
</template>

<script>
import { userInfo, userName, removeBind } from "@/api/user";
import { isUserName } from "@/utils/validate";
export default {
  components: {},
  props: {},
  data() {
    return {
      show: false,
      user: {
        note:{}
      },
      form: {
        user_name: "",
      },
    };
  },
  computed: {},
  methods: {
    async getUserInfo() {
      try {
        let { data } = await userInfo();
        this.user = data;
      } catch (error) {
        console.log(error.response.data);
      }
    },
    async removebind() {
      await removeBind({ scheme: 1 });
      this.getUserInfo();
      this.$toast("解除成功!");
    },
    actionClose(action, done) {
      // console.log(action);
      if (action === "confirm") {
        this.rules().then((valid) => {
          if (!valid) {
            done(false);
          } else {
            done();
          }
        });
      }else{
        done();
      }
    },
    logout() {
      this.$store.dispatch("LogOut").then(() => {
        location.reload(); // In order to re-instantiate the vue-router object to avoid bugs
      });
    },
    save() {
      this.rules().then(async (valid) => {
        if (valid) {
          await userName({ user_name: this.form.user_name });
          this.getUserInfo();
          this.$toast("你的是新用户名: " + this.form.user_name);
        }
      });
    },
    async rules() {
      if (this.form.user_name !== "") {
        if (!isUserName(this.form.user_name)) {
          this.$toast("用户名不符合规则");
          return false;
        }
      } else {
        this.$toast("请输入用户名");
        return false;
      }
      return true;
    },
  },
  created() {
    if (this.$route.query.name) {
      this.activeName = this.$route.query.name;
    }
    this.$nextTick(() => {
      this.getUserInfo();
    });
  },
  mounted() {},
};
</script>
<style lang="scss" scoped>
@import "@/assets/styles/mobile/form.scss";
.account-wrapper {
  width: 100%;
  height: 100%;
  //form
  .form-wrapper {
    padding: 10px;
    .input-wrapper {
      margin-bottom: 0;
    }
  }
  .head {
    width: 100%;
    height: 170px;
    padding: 0 20px;
    box-sizing: border-box;
    position: relative;
    background-color: #f6f6f6;
    .head-bg {
      position: absolute;
      top: 0;
      right: 0;
      width: 100%;
      height: 105px;
      background: url("../../../../static/image/mobile/personal_headbg.png")
        no-repeat;
      background-size: cover;
    }
    .center {
      position: relative;
      .logout {
        position: inherit;
        width: 100%;
        font-size: 0.8rem;
        text-align: right;
        color: #fff;
        line-height: 30px;
      }
      .user-info {
        position: inherit;
        width: 100%;
        min-height: 110px;
        margin: 0 auto;
        background-color: #fff;
        box-shadow: 0px 0px 27px 0px rgba(196, 137, 71, 0.14);
        border-radius: 6px;
        display: flex;
        padding: 25px 0 0 30px;
        overflow: hidden;
        box-sizing: border-box;
        .image {
          width: 60px;
          height: 60px;
          border-radius: 5px;
          overflow: hidden;
          margin-right: 15px;
          .van-image {
            width: 100%;
            height: 100%;
          }
        }
        ul {
          position: inherit;
          z-index: 999;
          font-size: 1rem;
          li {
            margin-block-end: 10px;
            display: flex;
            align-items: baseline;
            span {
              display: inline-block;
              width: 100px;
              color: #666666;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              margin-right: 20px;
            }
            i {
              cursor: pointer;
              color: #e5b467;
            }
          }
        }
        .right-bg {
          position: absolute;
          right: 0;
          top: 0;
          width: 50px;
          height: 100%;
          background: url("../../../../static/image/mobile/personal_right_bg.png")
            no-repeat;
          background-size: 100% 100%;
          height: 100%;
        }
      }
    }
  }
  .personal-title {
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 0.95rem;
    text-indent: 1px;
    letter-spacing: 1px;
    padding: 0 15px;
    box-sizing: border-box;
    color: #999999;
    background-color: #f6f6f6;
  }
  .van-cell-group {
    .not-color {
      color: #e5b467;
    }
    .van-cell__title {
      span {
        font-size: 1rem;
        letter-spacing: 2px;
        color: #333333;
        font-weight: normal;
        font-stretch: normal;
      }
      .custom-title {
        display: inline-flex;
        width: 4em;
        margin-left: 4px;
      }
      .custom-name {
        font-size: 0.9rem;
        color: #666666;
      }
      .icon-font {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        box-sizing: border-box;
        .iconfont {
          font-size: 1rem;
          color: #fff;
        }
      }
      .wechat {
        background-color: #22ac38;
      }
      .zhifubao {
        background-color: #73befb;
      }
    }
  }
}
</style>


